<script>
export default {
  name: "customeCustomerinfosearch",
  data() {
    return {
      ruleForm: { customer_genre: "0" },
      company: { options: [] },
      province: { options: [] },
      city: { options: [] },
      area: { options: [] },
      customerType: { options: [] },
    };
  },
  created() {
    let vm = this;
    this.$axios({
      method: "get",
      url: this.$api.getCustomerInfo,
      params: {
        customer_id: this.$route.query.id,
        associated_customer_id: this.$route.query.associated_customer_id,
      },
    }).then(({ data }) => {
      if (data.code == 200) {
        vm.ruleForm = data.data;
        vm.ruleForm.is_disabled = "" + data.data.is_disabled + "";
        vm.ruleForm.customer_genre = "" + data.data.customer_genre + "";
      }
    });
  },
  methods: {
    goBack() {
      this.$router.back();
    },
  },
};
</script>
